.results {
	height: 100%;
	min-width: 300px;
	flex: 2 1 0;
	display: flex;
	justify-content: center;
}

.content {
	display: flex;
	flex-direction: column;
}

.results-items {
	flex: 1 1 0;
	display: flex;
	align-items: center;
	flex-direction: column;
}

.page-links,
.links {
	margin: 1rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.page-link {
	max-width: 2rem;
	padding: .5rem;
}

.page-link:hover {
	cursor: pointer;
}
